import React from 'react'
//antd
import { Carousel } from 'antd'


// //components
// import NextArrow from '../components/NextArrow'
// import PrevArrow from '../components/PrevArrow'

//styles
import '../styles/EventCarousel.css'

//Webpage Caro
import Caro1 from '../resources/Topup500.png'
import Caro2 from '../resources/Topup1000.png'
import Caro3 from '../resources/Topup1500.png'
import Caro4 from '../resources/Topup4000.png'
import Caro5 from '../resources/Topup6000.png'
import Caro6 from '../resources/Topup8500.png'
import Caro7 from '../resources/JE98.png'
import Caro8 from '../resources/Event_EXRC.png'
import Caro9 from '../resources/SPOILER_KOTSV.jpg'


const EventCarousel = () => {

    const imgStyle = {
        // border: '10px solid  #bebebe'
        width: 750, height: 400,
    }
    return (
        <div>
            <div className="glowing-container" >
                <Carousel arrows effect="fade" autoplay >
                    <div>
                        <img style={imgStyle } src={Caro1} alt="Caro1" />
                    </div>
                    <div>
                        <img style={ imgStyle  } src={Caro2} alt="Caro2" />
                    </div>
                    <div>
                        <img style={ imgStyle } src={Caro3} alt="Caro3" />
                    </div>
                    <div>
                        <img style={ imgStyle} src={Caro4} alt="Caro4" />
                    </div>
                    <div>
                        <img style={ imgStyle } src={Caro5} alt="Caro5" />
                    </div>
                    <div>
                        <img style={ imgStyle } src={Caro6} alt="Caro6" />
                    </div>
                    <div>
                        <img style={ imgStyle } src={Caro7} alt="Caro7" />
                    </div>
                    <div>
                        <img style={ imgStyle } src={Caro8} alt="Caro8" />
                    </div>
                    <div>
                        <img style={imgStyle } src={Caro9} alt="Caro9" />
                    </div>
                </Carousel>
            </div>
        </div>

    )
}

export default EventCarousel